<template>
  <div id="app">
    <h1>Llistat de Preguntes</h1>

    <!-- Botones para listar y agregar preguntas -->
    <div class="buttons">
      <button @click="fetchQuestions" class="list-question-button">Llistat Pregunta</button>
      <button @click="showAddQuestionModal" class="add-question-button">Afegir Pregunta</button>
    </div>

    <!-- Mostrar preguntas en la lista -->
    <div v-for="pregunta in preguntes" :key="pregunta.id" class="question-card">
      <div class="question-content">
        <img :src="pregunta.imatge" alt="Pregunta Imagen" class="question-image" />
        <p class="question-text">{{ pregunta.pregunta }}</p>
      </div>

      <div class="responses">
        <p>Respostes:</p>
        <div v-for="resposta in pregunta.respostes" :key="resposta.id" class="response-option">
          <button 
            @click="selectAnswer(pregunta.id, resposta.id, pregunta.resposta_correcta)" 
            :class="{'correct': resposta.id === selectedAnswerId[pregunta.id], 'incorrect': selectedAnswerId[pregunta.id] && resposta.id !== pregunta.resposta_correcta}" 
            class="select-answer-button">
            {{ resposta.etiqueta }}
          </button>
        </div>
      </div>

      <button @click="editQuestion(pregunta.id)" class="edit-current-question-button">Editar Pregunta</button>
      <button @click="deleteQuestion(pregunta.id)" class="delete-question-button">Eliminar Pregunta</button>
    </div>

    <!-- Modal para añadir preguntas -->
    <div v-if="showModal" class="add-question-modal">
      <h2>Afegir Pregunta</h2>
      <form @submit.prevent="addQuestion">
        <div class="form-group">
          <label for="pregunta">Pregunta:</label>
          <input id="pregunta" v-model="newQuestion.pregunta" placeholder="Escribe la pregunta" required />
        </div>
        <div class="form-group">
          <label for="imatge">URL de la imagen:</label>
          <input id="imatge" v-model="newQuestion.imatge" placeholder="Escribe la URL de la imagen" required />
        </div>
        <div class="form-group">
          <label for="resposta">Respuesta:</label>
          <input id="resposta" v-model="newAnswerLabel" placeholder="Escribe una respuesta" required />
          <button @click.prevent="addAnswer">Añadir Respuesta</button>
        </div>
        <div class="form-group">
          <label for="respuestaCorrecta">Respuesta Correcta (ID):</label>
          <input id="respuestaCorrecta" v-model="newQuestion.resposta_correcta" type="number" placeholder="ID de respuesta correcta" required />
        </div>
        <button type="submit">Guardar</button>
        <button type="button" @click="closeModal">Cancelar</button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      preguntes: [],
      newQuestion: {
        pregunta: '',
        imatge: '',
        respostes: [],
        resposta_correcta: null,
      },
      newAnswerLabel: '',
      showModal: false,
      selectedAnswerId: {},
    };
  },
  methods: {
    fetchQuestions() {
      fetch('/api/preguntes') // Asegúrate de que esta URL es correcta
        .then(response => response.json())
        .then(data => {
          this.preguntes = data; // Almacena las preguntas obtenidas
        })
        .catch(error => console.error('Error fetching questions:', error));
    },
    showAddQuestionModal() {
      this.showModal = true;
    },
    closeModal() {
      this.showModal = false;
      this.resetNewQuestion();
    },
    resetNewQuestion() {
      this.newQuestion = {
        pregunta: '',
        imatge: '',
        respostes: [],
        resposta_correcta: null,
      };
      this.newAnswerLabel = '';
    },
    addAnswer() {
      if (this.newAnswerLabel) {
        const newAnswer = {
          etiqueta: this.newAnswerLabel,
        };
        this.newQuestion.respostes.push(newAnswer);
        this.newAnswerLabel = '';
      } else {
        alert('Por favor, completa el campo de respuesta.');
      }
    },
    addQuestion() {
      if (
        !this.newQuestion.pregunta ||
        !this.newQuestion.imatge ||
        this.newQuestion.respostes.length === 0 ||
        this.newQuestion.resposta_correcta === null
      ) {
        alert('Por favor, completa todos los campos necesarios.');
        return;
      }

      fetch('/api/preguntes', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(this.newQuestion),
      })
        .then(response => {
          if (!response.ok) {
            throw new Error('Network response was not ok');
          }
          return response.json();
        })
        .then(data => {
          this.preguntes.push(data);
          this.resetNewQuestion();
          this.closeModal();
        })
        .catch(error => {
          console.error('Error adding question:', error);
          alert('Error al guardar la pregunta. Inténtalo de nuevo.');
        });
    },
    editQuestion(id) {
      const question = this.preguntes.find(p => p.id === id);
      if (question) {
        this.newQuestion = { ...question };
        this.showModal = true;
      }
    },
    selectAnswer(questionId, answerId, correctAnswerId) {
      this.selectedAnswerId[questionId] = answerId;
      if (answerId === correctAnswerId) {
        alert('Correcto!');
      } else {
        alert('Incorrecto, intenta de nuevo.');
      }
    },
    deleteQuestion(id) {
      fetch(`/api/preguntes/${id}`, {
        method: 'DELETE',
      })
        .then(response => {
          if (response.ok) {
            this.preguntes = this.preguntes.filter(p => p.id !== id);
          } else {
            throw new Error('Failed to delete');
          }
        })
        .catch(error => {
          console.error('Error deleting question:', error);
        });
    },
  },
  mounted() {
    this.fetchQuestions(); // Puedes eliminar esto si solo quieres cargar preguntas al hacer clic
  },
};
</script>


